import * as THREE from 'three';
import { CreateScene, CLOUDS, FetchData } from 'common';
import { conf__1 as conf } from 'conf';

var __ = {};

__.conf = conf;
console.log(conf);

CreateScene(__, {});


function render() {
  requestAnimationFrame(render);
  /*
  if (__.mesh) {
    __.mesh.rotation.x += 0.0005;
    __.mesh.rotation.y += 0.0025;
  }
  */
  __.renderer.render(__.scene, __.camera);
}


FetchData('data/2015223-23.json').then(function(data) {
  let emis = CLOUDS.data0(__, data);
  __.mesh = emis;
  __.mesh.rotation.x = 0.5;
  __.scene.add(emis);
  
  __.camera = CLOUDS.data0.createCamera(__, data);
  
  document.addEventListener('mousemove', onDocumentMouseMove, false);
  document.addEventListener('mouseup', onDocumentMouseUp, false);
  document.addEventListener('mousedown', onDocumentMouseDown, false);
  document.addEventListener('mousewheel', onDocumentMouseWheel, false);
  render();
});

function onDocumentMouseMove(event) {
  if (__.mouse && __.mesh) {
    let { clientX:x0, clientY:y0 } = event;
    __.mesh.rotation.y += 0.005 * (x0 - __.mouse.x);
    __.mesh.rotation.x += 0.005 * (y0 - __.mouse.y);
    __.mouse.x = x0;
    __.mouse.y = y0;
  }
}

function onDocumentMouseDown(event) {
  __.mouse = {
    x: event.clientX,
    y: event.clientY,
  } 
}

function onDocumentMouseUp(event) {
  __.mouse = undefined;
}

function onDocumentMouseWheel(event) {
  if (__.camera) {
     __.camera.position.z *= 1000 / (1000.0 + event.deltaY);
  }
}
